.description-list {
  background-color: $background-color-primary;
}

.description-list__item {
  padding: 8px 5px 0 47px;
  position: relative;
  margin-bottom: 22px;

  @include tablet {
    box-sizing: border-box;
    width: 33%;
    padding: 156px 36px 58px;
  }
}

.description-list__item:last-child {
  margin-bottom: 0;
}

.description-list__title {
  margin: 0;
  margin-bottom: 16px;
  font-weight: 700;

  @include tablet {
    text-align: center;
    font-size: 21px;
  }
}

.description-list__text {
  margin: 0;

  @include tablet {
    text-align: center;
  }
}

.description-list__item::before {
  content: "";
  position: absolute;
  width: 40px;
  height: 38px;
  background-repeat: no-repeat;
  left: -6px;
  bottom: 50%;
  transform: translateY(-50%);

  @include tablet {
    top: 78px;
    left: 50%;
    transform: translateX(-50%);
  }
}

.description-list__item--living::before {
  background-image: url("../../img/decorative/icon-house.svg");
}

.description-list__item--food::before {
  background-image: url("../../img/decorative/icon-food.svg");
}

.description-list__item--gifts::before {
  background-image: url("../../img/decorative/icon-gift.svg");
}
